import { Component, OnInit } from '@angular/core';
import { ChartsModule } from 'ng2-charts/ng2-charts';
declare const AmCharts: any;

import '../../../../assets/charts/amchart/amcharts.js';
import '../../../../assets/charts/amchart/amcharts.js';
import '../../../../assets/charts/amchart/gauge.js';
import '../../../../assets/charts/amchart/pie.js';
import '../../../../assets/charts/amchart/serial.js';
import '../../../../assets/charts/amchart/light.js';
import '../../../../assets/charts/amchart/ammap.js';
import '../../../../assets/charts/amchart/worldLow.js';
import '../../../../assets/charts/amchart/continentsLow.js';
import { DashboardService } from '../../services/dashboardService';
import { Http } from '@angular/http';
import { DashboadData } from '../../models/dashboard.Model.js';
import { LogisticsDashboardModel } from '../../models/logisticsDashboardModel';

import { AmChartsService, AmChart } from "@amcharts/amcharts3-angular";
import { ENGINE_METHOD_DIGESTS } from 'constants';
import { PharmacyDashboardModel } from '../../models/pharmacyDashboardModel.js';

@Component({
  selector: 'app-ecommerce',
  templateUrl: './ecommerce.component.html',
  styleUrls: [
    './ecommerce.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [DashboardService, AmChartsService]
})
export class EcommerceComponent implements OnInit {

  public DashboadData: DashboadData;
  public PharmacyDashboardData:  PharmacyDashboardModel;
  public LogisticsDashboardData: LogisticsDashboardModel;

  public gotData: boolean = false;
  public GotLogisticData: boolean = false;
  public GotPharmacyData: boolean = false;
  public gotDataLoader:boolean = true;

  public top5Chemists = [];
  public session_obj: any;
  private chart: AmChart;
  public OrderStatuses = {
    pending: 100,
    confirmed: 200,
    ontheway: 300,
    notshipped: 400,
    shipped: 500,
    tobedelivered: 600,
    canceled: 700,
    completed: 800,
    returned: 900,
    delivered: 1000
  }
  options: any = {
    position: ['bottom', 'right'],
  };

  constructor(public _dashboardService: DashboardService, private AmCharts: AmChartsService) {

    this.DashboadData = new DashboadData();
    this.LogisticsDashboardData = new LogisticsDashboardModel();
    this.PharmacyDashboardData = new PharmacyDashboardModel();
    this.session_obj = JSON.parse(localStorage.getItem('session_obj'));

    
  }



  createStockChart(){
    this.chart = this.AmCharts.makeChart( "statistics_chart", {
      "type": "serial",
      "theme": "light",
      "dataProvider": [ {
        "country": "USA",
        "visits": 2025
      }, {
        "country": "China",
        "visits": 1882
      }, {
        "country": "Japan",
        "visits": 1809
      }, {
        "country": "Germany",
        "visits": 1322
      }, {
        "country": "UK",
        "visits": 1122
      }, {
        "country": "France",
        "visits": 1114
      }, {
        "country": "India",
        "visits": 984
      }, {
        "country": "Spain",
        "visits": 711
      }, {
        "country": "Netherlands",
        "visits": 665
      }, {
        "country": "Russia",
        "visits": 580
      }, {
        "country": "South Korea",
        "visits": 443
      }, {
        "country": "Canada",
        "visits": 441
      }, {
        "country": "Brazil",
        "visits": 395
      } ],
      "valueAxes": [ {
        "gridColor": "#FFFFFF",
        "gridAlpha": 0.2,
        "dashLength": 0
      } ],
      "gridAboveGraphs": true,
      "startDuration": 1,
      "graphs": [ {
        "balloonText": "[[category]]: <b>[[value]]</b>",
        "fillAlphas": 0.8,
        "lineAlpha": 0.2,
        "type": "column",
        "valueField": "visits"
      } ],
      "chartCursor": {
        "categoryBalloonEnabled": false,
        "cursorAlpha": 0,
        "zoomable": false
      },
      "categoryField": "country",
      "categoryAxis": {
        "gridPosition": "start",
        "gridAlpha": 0,
        "tickPosition": "start",
        "tickLength": 20
      }
    
    } );
  }

  ngOnInit() {

    this.checkOut();

    /* if (AmCharts.isReady) {
      this.createStockChart();
  
  } else {
      AmCharts.ready(function () {
          this.createStockChart();
      });
  } */
   

   

  }


  public barChartOptions: any = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  public barChartLabels: string[] = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartType: string = 'bar';
  public barChartLegend: boolean = true;

  public barChartData: any[] = [
    { data: [65, 59, 80, 81, 56, 55, 40], label: 'Series A' },
    { data: [28, 48, 40, 19, 86, 27, 90], label: 'Series B' }
  ];

  // events
  public chartClicked(e: any): void {
    console.log(e);
  }

  public chartHovered(e: any): void {
    console.log(e);
  }

  public randomize(): void {
    // Only Change 3 values
    let data = [
      Math.round(Math.random() * 100),
      59,
      80,
      (Math.random() * 100),
      56,
      (Math.random() * 100),
      40];
    let clone = JSON.parse(JSON.stringify(this.barChartData));
    clone[0].data = data;
    this.barChartData = clone;
    /**
     * (My guess), for Angular to recognize the change in the dataset
     * it has to change the dataset variable directly,
     * so one way around it, is to clone the data, change it and then
     * assign it;
     */
  }


  HidingAllDataFor() {
    this.GotLogisticData = false;
    this.gotData = false;
    this.GotPharmacyData = false;
  }


  checkOut() {

    this.HidingAllDataFor();
    if (this.session_obj.UserRole == 1) {
      
      this._dashboardService.getDashboardDataForAdmin().subscribe(a => {


        if (a.code == 200) {

          this.gotDataLoader = false;
          this.DashboadData = new DashboadData();
          this.DashboadData = a.data;
          this.gotData = true;
          for (var i = 0; i < this.DashboadData.LatestOrdersList.length; i++) {

            var myDate = new Date(parseFloat(this.DashboadData.LatestOrdersList[i].ReceivedTime) * 1000);
            this.DashboadData.LatestOrdersList[i].ReceivedTime = myDate.toLocaleString();


            for (var prop in this.OrderStatuses) {

              if (this.DashboadData.LatestOrdersList[i].OrderStatus == this.OrderStatuses[prop]) {

                this.DashboadData.LatestOrdersList[i].OrderStatus = prop;

              }
            }

          }

        }





      })

    } else if (this.session_obj.UserRole == 2) {

      this._dashboardService.getDashboardDataForPharmacy(this.session_obj.ChemistId).subscribe(a => {
        console.log(a);
        if(a.code == 200){
          this.gotDataLoader = false;
          this.GotPharmacyData = true;
          this.PharmacyDashboardData = a.data;

          for (var i = 0; i < this.PharmacyDashboardData.ListOfLatestOrdersPlaced.length; i++) {

            var myDate = new Date(parseFloat(this.PharmacyDashboardData.ListOfLatestOrdersPlaced[i].ReceivedTime) * 1000);
            this.PharmacyDashboardData.ListOfLatestOrdersPlaced[i].ReceivedTime = myDate.toLocaleString();


            for (var prop in this.OrderStatuses) {

              if (this.PharmacyDashboardData.ListOfLatestOrdersPlaced[i].OrderStatus == this.OrderStatuses[prop]) {
                
                this.PharmacyDashboardData.ListOfLatestOrdersPlaced[i].OrderStatus = prop;

              }
            }

          }
        }
      })

    } else if (this.session_obj.UserRole == 5) {

      this._dashboardService.getDashboardDataForLogistics(this.session_obj.UserRole).subscribe(a => {
        console.log(a);

        if (a.code == 200) {
          this.gotDataLoader = false;
          this.LogisticsDashboardData = new LogisticsDashboardModel();
          this.LogisticsDashboardData = a.data;
          this.GotLogisticData = true;

          for (var i = 0; i < this.LogisticsDashboardData.ListOfLatestIncomingOrders.length; i++) {

            var myDate = new Date(parseFloat(this.LogisticsDashboardData.ListOfLatestIncomingOrders[i].ReceivedTime) * 1000);
            this.LogisticsDashboardData.ListOfLatestIncomingOrders[i].ReceivedTime = myDate.toLocaleString();


            for (var prop in this.OrderStatuses) {

              if (this.LogisticsDashboardData.ListOfLatestIncomingOrders[i].OrderStatus == this.OrderStatuses[prop]) {
                
                this.LogisticsDashboardData.ListOfLatestIncomingOrders[i].OrderStatus = prop;

              }
            }

          }

        }
      })



    }




  }












 

}



